
/* eslint-disable no-restricted-globals */
/* eslint-disable max-len */
import Vue from 'vue'
import {
  _T,
  handleApiResult,
  sprintf,
  getPlatform,
  getArgsFromUrl,
  fixDomain,
} from '@badam/common-utils'
import store from '@/store'
// import Reward2 from '@/components/reward-modal/index2.vue'
import Reward2 from '@/components/new-reward/index.vue'
import { formatTime, getUUid, isStart, isEnd } from '@/utils'
import { throttle } from 'lodash'
import { showModal } from '@/components/base/modal/modal.vue'
import { showToast } from '@badam/vue-components'
import { getActInfo } from '@/store/action'
import {
  sendThanksToInvitors,
  RecallUserInviter,
  getUserInviters,
  receiveReward,
  getVersionConfig,
  getShareDetail,
} from '@/api'

export default Vue.extend({
  props: ['type'],
  computed: {
    // nickname() {
    //   return store.state.myInfo.nickname || ''
    // },
    // icon() {
    //   return store.state.myInfo.icon || ''
    // },
    canGet() {
      return store.state.actInfo.aim_info?.reward_status !== 3 && isStart() && !isEnd()
    },
    rewardStatus() {
      return store.state.actInfo.aim_info?.reward_status === 3
    },
    text2(): string {
      if (this.type === 1) {
        return _T('Sawa为你准备了回归礼包，欢迎回归，快来领取吧！')
      }

      if (this.type === 3) {
        return _T('好久不见，Sawa为你准备了回归礼包，欢迎回归，快登录Sawa去领取吧！')
      }

      const text1 = sprintf(_T('亲爱的{username}，好久不见'), {
        username: this.nickname,
      })
      const text2 = _T('你的好友为你准备了回归礼包，欢迎回归，快来领取吧！')
      return `<div>${text1}</div><div>${text2}</div>`
    },
    rewardsList() {
      return store.state.rewardConfig.comeGfit
    },
    selectOne() {
      const flag1 = (this as any).inviters.filter((item: any) => item.selected === true).length > 0
      const flag2 =
        (this as any).inviters.filter((item: any) => item.is_thanks === false).length > 0
      return flag1 && flag2
    },
    isIphone() {
      return getPlatform().isIPhone
    },
  },
  mounted() {
    this.resolveInfo()
    this.getConfig()
  },
  data() {
    return {
      query: '',
      inviters: [] as RecallUserInviter[],
      loading: false,
      version: 0,
      icon: '',
      nickname: '',
      timer: 0 as any,
      isPush1: false,
      isPush2: false,
      isPush3: false,
      isPush4: false,
    }
  },
  methods: {
    _T,
    sprintf,
    formatTime,
    fixDomain,
    onTouchStart(n: number) {
      if (n === 1) {
        this.isPush1 = true
      } else if (n === 2) {
        this.isPush2 = true
      } else if (n === 3) {
        this.isPush3 = true
      } else {
        this.isPush4 = true
      }
    },
    onTouchEnd(n: number) {
      if (n === 1) {
        this.isPush1 = false
      } else if (n === 2) {
        this.isPush2 = false
      } else if (n === 3) {
        this.isPush3 = false
      } else {
        this.isPush4 = false
      }
    },
    getPushBg() {
      if (!this.isPush1) {
        return {}
      }
      if (this.canGet) {
        return {
          backgroundImage:
            'url(data:image/png;base64,) !important',
          paddingTop: '27px',
          backgroundPosition: '0 5px',
          backgroundRepeat: 'no-repeat',
        }
      }
      return {
        backgroundImage:
          'url(data:image/png;base64,)',
        paddingTop: '27px',
        backgroundPosition: '0 4px',
        backgroundRepeat: 'no-repeat',
      }
    },
    async resolveInfo() {
      // const info = getArgsFromUrl('info')
      const info = new URL(window.location.href).searchParams.get('info')
      if (info) {
        const res = await getShareDetail(info)
        if (handleApiResult(res)) {
          const [, { data }] = res
          const { nickname, avatar } = data
          this.icon = avatar
          this.nickname = nickname
        }
      }
    },
    async getConfig() {
      const res = await getVersionConfig('user_recall_202407')
      if (handleApiResult(res)) {
        const data = JSON.parse(res[1].data.result)
        if (getArgsFromUrl('bundleid') === 'com.sawa.ludo') {
          this.version = data.apple.ludo_version
          return
        }
        const { isIPhone } = getPlatform()
        if (isIPhone) {
          this.version = data.apple.ksa_version
        } else if (getArgsFromUrl('bundleid') === 'com.badambiz.pk.saudi.huawei') {
          this.version = data.android.huawei
        } else {
          this.version = data.android.ksa_version
        }
      }
    },
    goToApple2(ludo: boolean) {
      if (ludo) {
        location.href =
          'https://apps.apple.com/pk/app/sawa-ludo-%D9%84%D9%88%D8%AF%D9%88-%D8%AF%D9%88%D9%85%D9%8A%D9%86%D9%88/id6443823893'
      } else {
        location.href = 'https://apps.apple.com/us/app/sawa-ksa/id1574856714'
      }
    },
    goToGoogle2() {
      location.href = 'https://play.google.com/store/apps/details?id=com.badambiz.pk.saudi'
    },
    openApp(ludo: boolean) {
      if (getPlatform().isAndroid) {
        this.goToGoogle2()
      } else {
        this.goToApple2(ludo)
      }
    },
    openSawa() {
      // shareTrace(getUUid(), '跳转sawa按钮点击')
      // ;(window as any).location.href = 'sawaksa://live_room?room_id=0'
      clearTimeout(this.timer)
      this.openApp(false)
      // this.timer = setTimeout(() => {
      //   this.openApp(false)
      // }, 2000)
    },
    openLudo() {
      // shareTrace(getUUid(), '跳转ludo按钮点击')
      // ;(window as any).location.href = 'sawaludo://live_room?room_id=0'
      clearTimeout(this.timer)
      this.openApp(true)
      // this.timer = setTimeout(() => {
      //   this.openApp(true)
      // }, 2000)
    },
    isNewVersion() {
      const platform = getPlatform()
      if (platform.isPc) {
        return true
      }
      const versionCode = Number(getArgsFromUrl('version_code'))
      return versionCode >= this.version
    },
    async receive() {
      if (!isStart()) return
      if (isEnd()) return
      if (store.state.actInfo.aim_info?.reward_status === 3) {
        showToast(_T('您已经领取过奖励啦，去房间看看吧'))
        return
      }

      // 接受
      const res = await receiveReward()
      if (handleApiResult(res)) {
        showModal(Reward2, {
          rewardList: store.state.rewardConfig.comeGfit,
        })
        getActInfo()
      } else if (res[1].result === 20059) {
        showToast(_T('活动已结束'))
      }
    },
    Tgo() {
      return throttle(this.receive, 1000, {
        leading: true,
        trailing: false,
      })
    },
    async send() {
      // todo
    },
    toggle(user: RecallUserInviter) {
      if (!user || !user.display_uid) {
        return
      }
      if (user.is_thanks) {
        return
      }
      const index = this.inviters.findIndex(
        (item: RecallUserInviter) => item.display_uid === user.display_uid
      )

      if (user.selected) {
        const u = { ...user, selected: false }
        this.$set(this.inviters, index, u)
      } else {
        const u = { ...user, selected: true }
        this.$set(this.inviters, index, u)
      }
    },
    //
    async getInvitersList(end = false) {
      if (end) {
        return
      }
      const [success, { result, data, message }] = await getUserInviters(this.query)
      if (success && result === 0) {
        // TODO:是否过滤已经感谢的用户
        // let inviters = data.users.filter((item: RecallUserInviter) => !item.is_thanks)
        let inviters = data.users
        inviters = inviters.map((item: RecallUserInviter) => {
          const selectedItem = { ...item, selected: true }
          return selectedItem
        })
        this.inviters = this.inviters.concat(inviters)
        this.query = data.query_token
        if (!data.users || data.users.length === 0) {
          this.getInvitersList(true)
        }
      }
    },

    async getAllInvitersList() {
      // 获取所有的邀请者
      this.getInvitersList()
    },
    async sendThanksToInvitors() {
      if (this.loading) {
        return
      }
      if (!this.selectOne) {
        return
      }
      this.loading = true
      const uids = this.inviters
        .filter((item: RecallUserInviter) => item.selected)
        .map((item: RecallUserInviter) => `${item.uid}`)
      const [success, { result, data, message }] = await sendThanksToInvitors(uids)
      this.loading = false
      if (success && result === 0) {
        // this.getInvitersList()
        uids.forEach((uid: string) => {
          const index = this.inviters.findIndex((item: RecallUserInviter) => `${item.uid}` === uid)
          const u = { ...this.inviters[index], is_thanks: true }
          this.$set(this.inviters, index, u)
        })
      } else if (result === 20059) {
        showToast(_T('活动已结束'))
      }
    },
  },
})
