import Vue from 'vue'
import '@badam/vue-tsx-support/enable-check'
import '@badam/vue-tsx-support/options/enable-nativeon'
import {
  calculateWindow,
  exec,
  ready,
  addWebpAvifSupport,
  initErrorReporter,
  reportSensors,
  metric,
  DayJs,
  TimeType,
  getPlatform,
  getArgsFromUrlHash,
  setLang,
  getServTs,
  appType,
  updateTsDelta,
} from '@badam/common-utils'
import { getActInfo, getMyInfo } from '@/store/action'
import Main from './mail.vue'
import { name, nanoid } from '../package.json'
import './index.module.scss'
import lang from './lang/index.ksa.json'

Vue.config.productionTip = false

if (getArgsFromUrlHash('lan') !== 'zh') {
  setLang(lang)
} else {
  setLang({}, '', true)
}

async function setTime() {
  const time = await getServTs(appType.sawa)
  updateTsDelta(time)
}

function init() {
  return Promise.all([setTime()])
}

exec(async () => {
  calculateWindow(750)

  try {
    await ready()
    init()
    DayJs.setOffset(TimeType.Ksa)
    await addWebpAvifSupport({
      avif: getPlatform().isIPhone,
    })
    initErrorReporter(name, nanoid)
    reportSensors('act_meaningful_content_loaded', true)
    metric.observeSourceMetric([
      {
        desc: 'banner',
        source_name: 'banner',
      },
    ])
  } catch (err) {
    console.error(err)
  }

  new Vue({
    render: (h) => h(Main),
  }).$mount('#app')
})
